import React, {Component} from 'react';

import { UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, Collapse, Navbar, NavbarToggler, Nav, NavItem, NavLink } from 'reactstrap';


class Menu extends Component {

   constructor(props){
        super(props);
        this.state = {
            isOpen: false
        };
        this.toggle = this.toggle.bind(this);
    }

    toggle() {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }

    render() {

        return <div className="menuPrincipal">

        
            <Navbar light expand="md">
                <NavbarToggler onClick={this.toggle}/>
                <Collapse isOpen={this.state.isOpen} navbar>
                    <Nav className="" navbar>
                        <NavItem active={ window.location.pathname==='/' }>
                            <NavLink href="/sgmrb">Inicio</NavLink>
                        </NavItem>
                       
                     
                        
                        {/* Men√∫ para la vista de root */}
                        <NavItem active={ window.location.pathname==='/juega' }>
                            <NavLink href="/sgmrb/#/juega">Jugar</NavLink>
                        </NavItem>
                        <UncontrolledDropdown nav inNavbar>
                            <DropdownToggle nav caret id="drop-subMenu" >
                               Entregar Material
                            </DropdownToggle>
                            <DropdownMenu className="menuRootView">
                            
                            <NavLink href="/sgmrb/#/survey"> Publica donacion  </NavLink>
                                <NavLink href="/sgmrb/#/survey"> Publica reciclaje </NavLink>
                               
                            </DropdownMenu>
                        </UncontrolledDropdown>

                        
                        <NavItem active={ window.location.pathname==='/mapa' }>
                            <NavLink href="/sgmrb/#/mapa">Ver mapa </NavLink>
                        </NavItem>

                        <NavItem active={ window.location.pathname==='/mapa' }>
                            <NavLink href="/sgmrb/#/about"> Sobre el trabajo </NavLink>
                        </NavItem>
                        
                    </Nav>
                </Collapse>
            </Navbar>
        </div>
    }
}

export default(Menu);


