                                    {this.props.errors.length > 0 && (
                                        <ul>
                                            {this.props.errors.map((error) => (
                                                <Row className="message">
                                                    <Col xs="12">
                                                        <span>{error.message}</span>
                                                    </Col>
                                                </Row>
                                            ))}
                                        </ul>
                                    )}

                                    <FormGroup>
                                        <AvField type="text" name="username" label="N√∫mero de documento de identidad" id="username" errorMessage="Documento no v√°lido" onChange={e => this.setState({ username: e.target.value })} validate={{
                                            required: { value: true },
                                            minLength: { value: 5 }
                                        }} />
                                    </FormGroup>
                                    <FormGroup>
