import React, {Component} from 'react';
import { Container, Row, Col, Button, Modal, ModalFooter, ModalHeader, ModalBody } from 'reactstrap';
import { Link } from "react-router-dom";

import RootGame from '../images/root/game.jpg';
import RootBusqueda from '../images/root/survey.jpg';
import RootMapa from '../images/root/mapa.jpg';

import RootDate from '../images/0_Banner votaci√≥n cuenta regresiva.png';

import Lupa from '../images/root/one.svg';
import Votacion from '../images/root/tres.svg';
import Llave from '../images/root/dos.svg';


import Bogota from '../images/root/Bogota.png';


class Root extends Component {

    constructor(props) {
        super(props);
        this.state = {
            modal: true
        };
        this.toggle = this.toggle.bind(this);
    }

    toggle() {
        this.setState({
            modal: !this.state.modal
        });
    }

    getDays(){
        let fin = new Date(2018, 11, 23).getDate();
        let inicio = new Date(new Date()).getDate();
        return fin-inicio+1;
    }
    
    render() {
        let days = this.getDays();
        return (
            <Container fluid id="container-content">
                
  
                
                

               
                <Row className="mt-3">
                    <Col xs="12" md="4" className="rootSearch">
                        <div className="voteSteps">
                            <p>Juega</p>
                        </div>
                        <Link to='/juega'>
                            <picture>
                                <source media="(max-width: 767px)" srcSet={RootGame}/>
                                <img src={RootGame} alt="Juega"/>
                            </picture>
                        </Link>
                    </Col>
                    <Col xs="12" md="4" className="rootVote mt-3 mt-md-0">
                        <div className="voteSteps">
                            <p>Publica </p>
                        </div>
                        <Link to='/survey'>
                            <picture>
                                <source media="(max-width: 767px)" srcSet={RootBusqueda}/>
                                <img src={RootBusqueda} alt="survey"/>
                            </picture>
                        </Link>
                    </Col>
                    <Col xs="12" md="4" className="rootVote mt-3 mt-md-0">
                        <div className="voteSteps">
                            <p>Mira el mapa </p>
                        </div>
                        <Link to='/mapa'>
                            <picture>
                                <source media="(max-width: 767px)" srcSet={RootMapa}/>
                                <img src={RootMapa} alt="Busqueda de Candidatos"/>
                            </picture>
                        </Link>
                    </Col>
                </Row>
            



                <Modal className="modalPedagogico" isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
                    <ModalHeader toggle={this.toggle}>
                      
                        <h1>Uso de la plataforma </h1>
                    </ModalHeader>
                    <ModalBody>
                          <Row>
                              <Col className="icono" sm="12" xs="12" md="2" lg="2">
                                  <img className="iconoModal" src={Lupa} alt=""/>
                              </Col>
                              <Col className="texto" sm="12" xs="12" md="10" lg="10">
                                  <h6><span className="bold-100">Paso 1: </span>juegue </h6>
                                  <p className="text-justify">
                                      Juegue y aprenda como reciclar 
                                      </p>
                              </Col>
                          </Row>
                        <Row>
                            <Col sm="12" xs="12" md="2" lg="2">
                                <img className="iconoModal" src={Llave} alt="Icono creaci√≥n de contrase√±a"/>
                            </Col>
                            <Col sm="12" xs="12" md="10" lg="10">
                                <h6><span className="bold-100">Paso 2: </span>informe donde esta el producto  </h6>
                                <p className="text-justify"> Realiza la publicaacion del material a reciclar o donar </p>
                            </Col>
                        </Row>
                        <Row>
                            <Col sm="12" xs="12" md="2" lg="2">
                                <img className="iconoModal" src={Votacion} alt="Icono "/>
                            </Col>
                            <Col sm="12" xs="12" md="10" lg="10">
                                <h6><span className="bold-100">Paso 3: </span>Mapa</h6>
                                <p className="text-justify"> Revisa nuestro mapa</p>
                            </Col>
                        </Row>
                    </ModalBody>
                    <ModalFooter>
                        <Button color="primary" onClick={this.toggle}>Continuar</Button>
                    </ModalFooter>
                </Modal>
            </Container>
        )
    }
}

export default (Root);


