import React, { Component } from "react";
import { connect } from "react-redux";
import { recoverPassword } from "../actions";
import { Link} from "react-router-dom";
import * as utilConstants from "../constants/utilConstants";

import { Jumbotron, Container, Row, Col, FormGroup, Button } from 'reactstrap';
import Loading from "./shared/Loading";
import { AvForm, AvField } from 'availity-reactstrap-validation';

import RecoverPassword from "./RecoverPassword"

class RecoverEmail extends Component {

    state = {
        email: ""
    };

    onSubmit = (e) => {
        e.preventDefault();
        this.props.sendEmail(this.state.email);
    };

    render() {
        const { loading, send_email } = this.props;

        if (loading) {
            return (
                <div>

                    <Loading />
                    <Row>
                        <Col className="containerMessage" xs="12" sm="12" md={{ size: 4, offset: 4 }} lg={{ size: 4, offset: 4 }}>
                            <Jumbotron>
                                <h1 className="display-4">Mensaje enviado</h1>
                                <p className="lead">
                                    Hemos enviado un correo electr√≥nico para que pueda restablecer su contrase√±a.<br />
                                    <span className="hardBold">Recuerde revisar la carpeta de spam</span> .
                            </p>
                                <p className="lead">
                                    <div className="rowButtons">
                                        <Button className="fix-border" color="primary" onClick={() => { document.location.href = '/changePassword' }}>Ir al inicio</Button>
                                    </div>
                                </p>
                            </Jumbotron>
                        </Col>
                    </Row>
            )
                </div>
            )
        }

        if (send_email) {
            return (

                <Row>
                    <Col className="containerMessage formSendEmail formSimpleCenter" xs="12" sm="12" md={{ size: 4, offset: 4 }} lg={{ size: 4, offset: 4 }}>
                        <Jumbotron>
                            <h1 className="display-4">Mensaje enviado</h1>
                            <p className="lead">
                                Hemos enviado un correo electr√≥nico para que pueda restablecer su contrase√±a.<br />
                                <span className="hardBold">Recuerde revisar la carpeta de spam</span> .
                            </p>
                            <p className="lead">
                                <div className="rowButtons">
                                    <Button className="fix-border" color="primary" onClick={() => { document.location.href ='/changePassword'}}>Ir al inicio</Button>
                                </div>
                            </p>
                        </Jumbotron>
                    </Col>
                </Row>
            )
        }

        return (
            <div  className="formSendEmail formSimpleCenter">
                <Row >
                    <Col className="titulo" xs="12" sm={{ size: 10, offset: 1 }} md={{ size: 8, offset: 2 }} lg={{ size: 6, offset: 3 }}>
                        <h1 className="h1">Recuperar contrase√±a</h1>
                     </Col>
                     <Col xs="12" sm={{ size: 10, offset: 1 }} md={{ size: 8, offset: 2 }} lg={{ size: 6, offset: 3 }} >
                        <AvForm onValidSubmit={this.onSubmit}>
                        <div class="background"></div>
                            {this.props.errors.length>0 && (
                                <ul>
                                    {this.props.errors.map((error) => (
                                        <Row className="message">
                                            <Col xs="12" sm="12" md="12" lg="12">
                                                <span>{error.message}</span>
                                            </Col>
                                        </Row>
                                    ))}
                                </ul>
                            )}
                            <FormGroup >
                                <AvField type="email" name="email" label="Ingrese su correo electr√≥nico" id="emailRescue" errorMessage="Email no v√°lido" onChange={e => this.setState({ email: e.target.value })} validate={{
                                    required: { value: true },
                                    email: { value: true }
                                }} />
                            </FormGroup>
                            <FormGroup>
                            <div className="rowButtons">
                                    <Button className="h1 flex-item" type="submit" onClick={() => { this.setState({
                                        send_email:true
                                    }) }}
                                    >Enviar</Button>
                                <div className="btnLink flex-item">
                                <Link to="/changePassword" >Inicio</Link>
                                </div>
                            </div>
                            </FormGroup>
                        </AvForm>
                    </Col>
                </Row>
            </div>
        )
    }
}

const mapStateToProps = state => {
    let errors = [];
    if (state.recoverPassword.errors) {
        errors = Object.keys(state.recoverPassword.errors).map((field) => {
            return { field, message: state.recoverPassword.errors[field] };
        })
    }
    return {
        errors,
        loading: state.recoverPassword.loading,
        send_email: state.recoverPassword.send_email
    };
};

const mapDispatchToProps = dispatch => {
    return {
        sendEmail: (email) => {
            return dispatch(recoverPassword.sendEmail(email));
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(RecoverEmail);


