import React, {Component} from 'react'
import {connect} from "react-redux";
import { auth } from "../../actions";
import Menu from "./Menu";

import { Container, Row, Col } from 'reactstrap';

import logo_consejos  from '../../images/header/1_logo consejos.png';
import logo_bogota_mejor  from '../../images/header/1_logo bogot√°.png';
import user_icon from '../../images/header/icono_usuario.png';
import logout_icon from '../../images/salir.png';

class Header extends Component {



    render() {
        let token = localStorage.getItem('token_voto');
        let name = '';
        let rols = [];
        let elector_id = 0;
        if(!!token){
            let parseToken = this.parseJwt(token);
            name = parseToken.nombres;
            rols = parseToken.grupos;
            elector_id = parseToken.elector_id;
        }

        return (
            <Container fluid className="Header" id="top" >

                {/* <Col className="contenedorLogos" xs="12" sm="12" md="12" lg="12">
                    <Row>
                        <Col xs={{size: 2, offset: 1}}>
                            <img className="img_header" src={logo_consejos} alt="Logo Consejos de Cultura y Deporte"/>
                        </Col>
                        <Col xs='6'>
                            <h1>
                                Sistema de 
                            </h1>
                        </Col>
                        <Col className="logo_alcaldia" xs='2'> 
                            <img className="img_header" src={logo_bogota_mejor} alt="Logo Alcaldia Mayor de Bogot√° Mejor para todos"/>
                        </Col>
                    </Row>
                </Col> */}

                <Row className="thirdLineHeader text-center justify-content-between">
                    <Col className="authCol" xs="12" sm="12" md={{size: 10, offset: 1}} lg={{size: 10, offset: 1}}>
                            
                            <Col className="menu" xs="12" sm="12" md="12" lg="12">
                                {
                                    (this.props.isAuthenticated === false) ?
                                        <Menu isAuthenticated={this.props.isAuthenticated} rols={rols} elector_id={elector_id}/> : <Menu/>
                                }

                            </Col>
                            {
                                this.props.isAuthenticated === true ?
                                <Col className="infoUser">

                                    <span className="welcome">Ha ingresado como: </span>
                                    <span className="name"> {name}</span>
                                    <img className="userIcon" src={ user_icon} alt="Usuario" onClick={ this.toggleTooltip }/>
                                    <span className="logout" onClick={()=>this.logout()}>
                                        Salir <img src={ logout_icon } alt="Salir"/>
                                    </span>
                                    {
                                        (this.state.tooltipOpen) ?
                                            <span className="logoutFull" onClick={()=>this.logout()}>
                                                Salir <img src={ logout_icon } alt="Salir"/>
                                            </span> : null
                                    }
                                </Col>  : null
                            }

                    </Col>
                </Row>
            </Container>
        )
    }
}



export default (Header);


