export const loadUser = () => {
    return (dispatch, getState) => {

        dispatch({type: "USER_LOADING"});

        let headers = { "Content-Type": "application/json"};
        let token = getState().auth.token;
        let body = JSON.stringify({ token });

        if(token){
            return fetch(`${process.env.REACT_APP_API_URL}/auth/verify-token/`, { headers, body, method: "POST" }).then((res) => {
                res.json().then((res) => {
                    if(res.status === 200){
                        dispatch({type: "USER_LOADED", user: res.data.token});
                        return res.data;
                    }else if (res.status >= 400 && res.status < 500){
                        if(res.data.non_field_errors){
                            dispatch({type:"AUTHENTICATION_ERROR", data: ['Su sesi√≥n se ha cerrado']});
                            return res.data.non_field_errors;
                        }else{
                            dispatch({type: "USER_LOADED", user: res.data});
                            return res.data;
                        }
                    }
                });
            });
        }else{
            dispatch({type:"AUTHENTICATION_ERROR", data: 'No autenticado'});
        }
    }
};

export const login = (username, password, g_recaptcha_response) => {
    return (dispatch, getState) => {

        if(g_recaptcha_response !== ''){
            let headers = {"Content-Type": "application/json"};
            let body = JSON.stringify({username, password, g_recaptcha_response});

            return fetch(`${process.env.REACT_APP_API_URL}/auth/login/`, { headers, body, method: "POST"}).then((res)=>{
                res.json().then((res) => {
                    if(res.status === 200){
                        dispatch({type: "LOGIN_SUCCESS", data: res.data.token });
                        return res.data.token;
                    }else if (res.status >= 400 || res.status <= 500) {
                        dispatch({type: "LOGIN_FAILED", data: res.data});
                        return res.data
                    }
                });

            }).catch((err)=>{
                dispatch({type: "LOGIN_FAILED", data: ['Error al autenticar usuario!']});
            });
        }else{
            dispatch({type: "CAPTCHA_FAILED", data: ['Captcha vac√≠o']});
            return ['Captcha vac√≠o'];
        }
    }
};

export const logout = () => {
    return (dispatch, getState) => {
        dispatch({type: 'LOGOUT_SUCCESSFUL'});
    }
};


