export const setNewPassword = (new_password1, new_password2, uid, token) => {
    return (dispatch, getState) => {

            dispatch({type: "PASSWORD_RECOVERING"});
    
            let headers = {"Content-Type": "application/json"};
            let body = JSON.stringify({new_password1, new_password2, uid, token});
            if(new_password1 === new_password2){
                return fetch(`${process.env.REACT_APP_API_URL}/auth/restablecimiento-clave/`, { headers, body, method: "POST"}).then((res)=>{
                    res.json().then((res) => {
                        if(res.status === 200){
                            dispatch({type: "PASSWORD_RECOVERED", data: res.data.new_password2 });
                            return res.data;
                        }else if (res.status >= 400 || res.status <= 500) {
                            let message = '';
                            if(res.data.new_password2){
                                message = res.data.new_password2[0];
                            }
                            if(res.data.token){
                                message = 'El tiempo para cambiar su contrase√±a ha expirado.'
                            }
                            dispatch({type: "PASSWORD_RECOVER_FAILED", data: [message]});
                            return res.data;
                        }
                    }).catch((err)=>{
                        dispatch({type: "PASSWORD_RECOVER_FAILED", data: ['Ingrese un dato v√°lido']});
                    });
                });
            }else{
                dispatch({type: "PASSWORD_RECOVER_FAILED", data: ['Las contrase√±as deben ser iguales']});
            }
    }
};

export const sendEmail = (email) => {
    return (dispatch, getState) => {
            if(email){
                dispatch({type: "EMAIL_SENDING"});

                let headers = {"Content-Type": "application/json"};
                let body = JSON.stringify({email});

                return fetch(`${process.env.REACT_APP_API_URL}/auth/restablecer-clave/`, { headers, body, method: "POST"}).then((res)=>{
                    res.json().then((res) => {
                        if(res.status === 200){
                            dispatch({type: "EMAIL_SEND", data: res.data })
                            return res.data;
                        }else if (res.status >= 400 || res.status <= 500) {
                            dispatch({type: "EMAIL_SEND_FAILED", data: res.data});
                            return res.data;
                        }
                    }).catch((err)=>{
                        console.log('Error tratando de enviar correo electr√≥nico');
                    });
                });
            }else{
                dispatch({type: "EMAIL_SEND_FAILED", data: 'Ingrese un correo valido'});
                return ['Ingrese un correo valido'];
            }
    }
};


