import React from 'react';

import { Route, Switch, BrowserRouter, Redirect} from 'react-router-dom';

import { HashRouter as Router} from 'react-router-dom';

import RecoverPassword from "./components/RecoverPassword";
import NotFound from "./components/NotFound";

import Login from "./components/Login";
import RecoverEmail from "./components/RecoverEmail";
import Root from "./components/Root";

import Game from "./components/game/game";
import Mapa from "./components/game/mapa";
import Survey from "./components/game/survey";

import About from "./components/game/about";
import Layout from "./components/Layout";


import logo from './logo.svg';
import './App.css';




import * as utilConstants from "./constants/utilConstants";



function App() {
  return (



    <Layout>
       <Router >
                    <Switch>
                        {/*Monitoreo*/}
                  
                        <Route exact path="/" component={Root}/>
                        <Route exact path="/juega" component={Game}/>
                        <Route exact path="/survey" component={Survey}/>
                        <Route exact path="/mapa" component={Mapa}/>

                        <Route exact path="/about" component={About}/>

                        {/* <Route exact path="/resetPassword" component={RecoverEmail}/> */}
                        {/* <Route exact path="/changePassword" component={RecoverPassword} /> */}
                        
                        {/* <Route exact path="/login" component={Login} /> */}
            
                        
                        <Route path="*" component={NotFound}/>


                    </Switch>
                </Router>
    </Layout>


  );
}

export default App;



