import React, {Component} from "react";
import {connect} from "react-redux";
import { recoverPassword} from "../actions";
import * as utilConstants from "../constants/utilConstants";
import { AvForm, AvField } from 'availity-reactstrap-validation';

import { Jumbotron, Container, Row, Col, FormGroup, Button } from 'reactstrap';
import Loading from "./shared/Loading";


class RecoverPassword extends Component {

    constructor(props){
        super(props);
        this.state = {
            ...this.state,
            uid: '',
            token: ''
        };
    }

    onSubmit = (e) => {
        e.preventDefault();

        this.props.setNewPassword(this.state.password_1, this.state.password_2, this.state.uid, this.state.token);
    };

    componentDidMount(){
        this.setState({
            uid: this.props.match.params.uid,
            token: this.props.match.params.token
        });
    }

    render() {
        const { loading, reset_password } = this.props;

        if(loading){
            return <Loading />
        }

        if(reset_password){
            return(
                <Row>
                    <Col className="message containerMessage" xs="12" sm="12" md={{ size: 4, offset: 4 }} lg={{ size: 4, offset: 4 }}>
                        <Jumbotron>
                            <h5 className="display-4">Contrase√±a actualizada</h5>
                            <p className="lead">
                                Su contrase√±a ha sido actualizada.
                            </p>
                            <p className="lead">
                                <div className="rowButtons">
                                    <Button className="fix-border" color="primary" onClick={() => {document.location.href='/login'}}>Ir al inicio</Button>
                             </div>
                            </p>
                        </Jumbotron>
                    </Col>
                </Row>
            )
        }

        return (
            <div className="formResetPassword formSimpleCenter formularioCrear">
                <Row>


                    <Col xs="12" sm={{ size: 10, offset: 1 }} md={{ size: 8, offset: 2 }} lg={{ size: 6, offset: 3 }}>
                        <Col className="titulo espacioInferior" xs="12" sm={{ size: 10, offset: 1 }} md={{ size: 12 }} lg={{ size: 12 }}>
                            <h1 className="h1">Crear contrase√±a</h1>
                            <p class="mensajeAyuda">
                                Tenga en cuenta que la contrase√±a debe contener:<br/>
                                <ul className="text-left">
                                    <li>M√≠nimo ocho (8) caracteres</li>
                                    <li>M√≠nimo un (1) n√∫mero</li>
                                    <li>M√≠nimo una (1) letra</li>
                                    <li>No hay diferencia entre may√∫sculas y min√∫sculas</li>
                                </ul>
                            </p>
                        </Col>

                        <Col xs="12" sm={{ size: 10, offset: 1 }} md={{ size: 12 }} lg={{ size: 12 }} >

                            <AvForm onSubmit={this.onSubmit}>
                                <div class="background"></div>

                                {this.props.errors.length>0 && (
                                    <ul>
                                        {this.props.errors.map((error) => (
                                            <Row className="message">
                                                <Col xs="12" sm="12" md="12" lg="12">
                                                    <span>{error.message}</span>
                                                </Col>
                                            </Row>
                                        ))}
                                    </ul>
                                )}

                                <FormGroup>
                                    <AvField type="password" name="password_1" label="Nueva contrase√±a" id="password_1" errorMessage="Contrase√±a no v√°lida" onChange={e => this.setState({password_1: e.target.value})} validate={{
                                        required: {value: true},
                                        minLength: {value: 8}
                                    }} />
                                </FormGroup>
                                <FormGroup>
                                    <AvField type="password" name="password_2" label="Ingrese nuevamente la contrase√±a" id="password_2" errorMessage="Las contrase√±as no coinciden" onChange={e => this.setState({password_2: e.target.value})} validate={{
                                        required: {value: true},
                                        minLength: {value: 8},
                                        match:{value:'password_1'}
                                    }} />
                                </FormGroup>
                                <p className="text-justify"><b>Importante:</b> Recuerde que si no puede ingresar a la plataforma puede votar de manera
                                presencial.</p>
                                <FormGroup>
                                    <div className="rowButtons">
                                        <Button className="flex-item h1" type="submit">Enviar</Button>
                                    </div>

                                </FormGroup>
                            </AvForm>
                        </Col>
                    </Col>
                </Row>
            </div>
        )
    }
}

const mapStateToProps = state => {
    let errors = [];
    if(state.recoverPassword.errors){
        errors = Object.keys(state.recoverPassword.errors).map((field) => {
            return { field, message: state.recoverPassword.errors[field] };
        })
    }
    return {
        errors,
        loading: state.recoverPassword.loading,
        reset_password: state.recoverPassword.reset_password
    };
};

const mapDispatchToProps = dispatch => {
    return {
        setNewPassword: (password_1, password_2, uid, token) => {
            return dispatch(recoverPassword.setNewPassword(password_1, password_2, uid, token));
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(RecoverPassword);


